.game-grid-container {
  display: flex;
  position: relative;
  flex-direction: column;/*  */
  padding: 0;
   /*  */
  min-height: 91vh; /*  */
}

.game-grid-big{
  display: flex;
  justify-content: center; 
  width: 100%;
  height: 90vh;
}

.game-grid {
  display: flex;
  flex-wrap: wrap;
  z-index: 1100;
  justify-content: flex-start; /*  */
  align-items: flex-start; /*  */
  margin-top: 0vh; /*  */
  margin-bottom: 0px; 
  /* margin-left: 5vw;
  margin-right: 5vw; */
  width: 90%;
  
}

.game-grid.hover {
  z-index: 1100;
}

/* Pagination and page items styles */
.pagination {
  display: flex;
  margin: auto;
  margin-bottom: 1vh;
  justify-content: center; /*  */
  gap: 1.1vw;
  height: 5vh;
  width: 100%; /* 100% */
}

.page-item {
  min-height: 5vh;
  border: none;
  background: none;
  cursor: pointer;
  color: rgba(253, 241, 255, 0.363); 
  font-size: clamp(0.9rem, 2.5vh, 1.7rem);
  transition: opacity 0.7s ease-out, transform 0.7s ease-out; /*  */
}

.page-item.active {
  color: rgba(163, 72, 162, 0.664);
}

.page-item:hover {
  opacity: 0.5; /*  */
  transform: scale(1.5); /*  */
}

.page-item:not(.active) {
  font-size: clamp(0.9rem, 2.5vh, 1.7rem);
  color: rgba(253, 241, 255, 0.363); 
  filter: drop-shadow(0px 0px 0.19vw rgb(0, 0, 0,1.0));/*  */
}

.game-grid-fade-in {
  animation: fadeIn 700ms ease-in-out;
}

@keyframes fadeIn {
  0%, 100% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.game-grid-fade-out {
  animation: fadeOut 700ms ease-in-out forwards;
}

@keyframes fadeOut {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}