.game-card {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-top: 1vw;
  margin-right: 2vw;
  margin-bottom: 1.5vh; /* Add some space at the bottom */
  margin-left: 2vw;
  transition: transform 0.7s ease-in-out;
  max-width: 11.7vw;
  min-width: 5vw;
  position: relative;
  overflow: visible;
  text-shadow: 1px 1px 0.7vh rgba(0, 0, 0, 0.99);
}

.game-card:hover {
  transform: scale(1.3);
  overflow: visible;
  z-index: 1100;
  /*  */
}

.game-image {
  width: 100%;   /*  */
  position: relative; /*  */
 /*  */
  aspect-ratio: 2 / 3; /* 2:3 */
  border-radius: 10px;
  box-shadow: 0px 4px 3vh rgba(0, 0, 0, 0.77);
  object-fit: cover;
}

.game-name-container {
  max-width: 100%;
  min-width: 100%;
  /*  .game-image  */
  overflow: hidden;
  /*  */
  white-space: nowrap;
  /*  */
  margin-top: 0.7vh;
  display: flex;
  justify-content: center; /*  */
  align-items: center; /*  */
  
}

/* GameCard.css */
.game-name {
  color: rgba(255, 255, 255, 0.801);
  font-size: 1rem;
  font-style: normal;
  font-weight: 600;
  /*  */
  white-space: nowrap;
  text-overflow: ellipsis;
  max-width: 200px;
  
  /*  */
}


@keyframes scrollText {

  0%,
  25% {
    transform: translateX(0);
  }

  50% {
    transform: translateX(-100%);
  }

  75%,
  100% {
    transform: translateX(0);
  }
}

.long-text {
  animation: scrollText 20s linear infinite;
  display: inline-block;
  /*  inline-block */
}

.game-rate-container {
  position: absolute;
  bottom: -2.3vh; /*  */
  left: 0;
  width: 100%; /*  */
  display: flex;
  justify-content: center;
  padding: 5px;
  box-sizing: border-box;
  border-radius: 8px; /*  */
}



.option-button {
  position: absolute;
  right: 0.5vw;
  top: 0.5vw;
  width: 2vh;
  height: 2vh;
  background: url(../../public/img/ICON/icons-menu-480.png) no-repeat center center;
  background-size: cover;
  opacity: 0;
  transition: opacity 0.5s;
}

.game-card:hover .option-button {
  opacity: 0.7;
}

.options-menu {
  visibility: hidden; /*  */
  opacity: 0; /* 0 */
  position: absolute;
  right: 0.5vw;
  top: 3.3vh;
  background: rgba(0, 0, 0, 0.3);
  backdrop-filter: blur(10px);
  border-radius: 1vh;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
  z-index: 100;
  transition: visibility 0s linear 0.7s, opacity 0.7s linear; /*  */
  color: white;
  font-size: 0.8rem;
}


.options-menu.active {
  visibility: visible;
  opacity: 1;
  
  transition-delay: 0s; /*  */
}

.option-item {
  padding: 1vh 1vw;
  cursor: pointer;
}

.option-item:hover {
  border-radius: 1vh;
  background-color: #33333393; /*  */
  
}

.option-item-rating {
  display: flex;
  align-items: center;
  justify-content: center;
}

.MuiRating-root {
  color: #793f41;
}

.option-item .MuiRating-root {
  margin-left: 8px;
}

.MuiRating-iconFilled {
  color: #ff3d47;
}

.MuiRating-iconEmpty {
  color: rgba(44, 14, 14, 0.7);
}

.MuiRating-iconHover {
  color: #ff6d75;

}


.tag-input-container {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  padding: 5px;
  position: absolute;
  bottom: 35px; /*  */
  left: 0;
  right: 0;
  background: rgba(0, 0, 0, 0.6); /*  */
  border-radius: 8px;
  box-sizing: border-box;
  justify-content: center;
  max-width: calc(100% - 10px);
  margin: 1.5vh 1vw;
  z-index: 150;
  backdrop-filter: blur(10px); /*  */
}

.tag {
  position: relative;
  color: #c4c4c4; /*  */
  border-radius: 12px;
  padding: 1vh 1vh;
  margin-right: 10px;
  display: flex;
  align-items: center;
  font-size: 0.79rem;
  pointer-events: auto;
  background: rgba(37, 16, 77, 0.1); /*  */
  backdrop-filter: blur(10px); /*  */
  z-index: 2;
}

.tag::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  backdrop-filter: blur(9px);
  border-radius: 12px;
  z-index: -1;
  box-shadow: 0px 0px 1vw rgba(0, 0, 0, 0.705);
}

input[type="text"] {
  border: none;
  outline: none;
  padding: 0.5vh;
  margin: 0;
  flex-grow: 1;
  max-width: calc(100% - 30px);
  background: rgba(255, 255, 255, 0.6); 
  border-radius: 5px; /*  */ /* Prevents overflow by considering padding and button width */
}


.tag-icon {
  width: 16px; /* Smaller icon */
  height: 16px; /* Smaller icon */
  margin-right: 5px; /* Adjust margin to visually balance the icon and text */
}

.tag-container {
  display: flex;
  position: absolute;
  bottom: 1.3vh; /* Adjusted to move the tags inside the game card at the bottom */
  left: 0.5vw; /* Starts tags from the left side */
  align-items: center;
  flex-wrap: wrap;
  pointer-events: none; /* Makes the container not block clicks on items below it, if necessary */
}

.game-image-container {
  position: relative; /*  */
}



/*  */
@keyframes game-card-enter {
  from {
    opacity: 0;
    transform: translateX(3vw);
  }
  to {
    opacity: 1;
    transform: translateX(0px);
  }
}

/*  */
@keyframes game-card-exit {
  from {
    opacity: 0;
    transform: translateX(-3vw);
  }
  to {
    opacity: 1;
    transform: translateX(0px);
  }
}

/*  */
.game-card {
  transition: opacity 0.9s ease, transform 0.9s ease;
}

.game-card-move-in {
  animation: game-card-enter 0.9s ease;
}

.game-card-move-out {
  animation: game-card-exit 0.9s ease;
}
